.skillbox{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height:300px;
    width: 1000px;
    border: 1px solid;
    border-radius: 15px;
    padding: 0 20px 0 20px;
    font-size: 30px;
    text-align: center;
}

.skillbox img{
    height: 100px;
    width: 100px;
}